<template>
  <header class="todo-header">
    <todo-input @keydown.enter="addTodo" v-model="todoInputModel"></todo-input>
    <todo-button @click="addTodo" label="Add to list"></todo-button>
  </header>
  <hr />
  <transition-group tag="ul" class="todo-list" appear name="fade-move">
    <todo-item
      v-for="todo in todoStateSorted"
      :key="todo"
      :item="todo"
      @todo-done="toggleDone(todo.id)"
      @todo-delete="removeTodo(todo.id)"
      @todo-edit="editTodo(todo.id, $event)"
    ></todo-item>
  </transition-group>
</template>

<script>
import TodoInput from "@/components/TodoInput.vue";
import TodoButton from "@/components/TodoButton.vue";
import TodoItem from "./TodoItem.vue";

import useTodos from "@/use/useTodos.js";

export default {
  setup() {
    const {
      todoInputModel,
      todoCount,
      addTodo,
      editTodo,
      removeTodo,
      todoStateSorted,
      toggleDone,
    } = useTodos();
    return {
      todoInputModel,
      todoCount,
      addTodo,
      editTodo,
      toggleDone,
      todoStateSorted,
      removeTodo,
    };
  },
  components: {
    TodoInput,
    TodoButton,
    TodoItem,
  },
};
</script>

<style scoped>
.fade-move-enter-from {
  opacity: 0;
  transform: translateY(-30px);
}

.fade-move-enter-to,
.fade-move-leave-from {
  transition: all 0.5s;
}

.fade-move-leave-to {
  transform: translateY(-30px);
  transition: all 0.5s;
  opacity: 0;
}

.fade-move-leave-active {
  position: absolute;
}
</style>